import React, { Fragment } from "react";
import { useDispatch } from "react-redux";
import { editItem } from "../store/todoActoins";

export default function SingleTodoItem(props) {
  const dispatch = useDispatch();
  const getTimeFromDate = (date) => {
    let currentDate = new Date();
    let itemDate = new Date(date);

    let itemTime = itemDate.getTime();
    let currentTime = currentDate.getTime();

    let finalTime = currentTime - itemTime,
      second = 1000,
      minute = second * 60,
      hour = minute * 60,
      day = hour * 24;
    let fDay = Math.floor(finalTime / day),
      fHour = Math.floor((finalTime % day) / hour),
      fMinute = Math.floor((finalTime % hour) / minute),
      fSecond = Math.floor((finalTime % minute) / second),
      fTime = "";
    if (fDay > 0) {
      fTime = fDay + "day";
    } else if (fDay < 0) {
      fTime = fHour + "hr";
    } else if (fHour > 0 && fHour < 24) {
      fTime = fHour + "hr";
    } else if (fMinute > 0 && fMinute < 60) {
      fTime = fMinute + "min";
    } else if (fSecond > 0 && fSecond < 60) {
      fTime = fSecond + "sec";
    }

    return fTime;
  };
  const data = props.items.map((item, index) => (
    <div
      className="w-full"
      key={item.id ? item.id.toString() : index.toString()}
    >
      <figure className={`todo__fig ${item.style}`}>
        <blockquote className="w-full">
          <div className="todo__fig--header">
            <div>
              <h5 className="todo__fig--name">- Ahmed Reda</h5>
              <span className="block leading-4 text-xs px-3">
                {getTimeFromDate(item.date)}
              </span>
            </div>
            <button
              className="mx-2"
              onClick={() => dispatch(editItem(item, index))}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-4 w-4"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                />
              </svg>
            </button>
          </div>
          <p className="p-2 py-3 break-words">{item.content}</p>
        </blockquote>
        <figcaption className="w-full pt-2 border-t">
          <h5 className="text-sm text-gray-500 py-2 px-1">{item.title}</h5>
        </figcaption>
      </figure>
    </div>
  ));

  return <Fragment>{data}</Fragment>;
}
