import { React, useRef } from "react";
import logo from "../assets/images/logo-todo.png";
import { useDispatch } from "react-redux";
import { openCreation } from "../store/todoActoins";
import "./headerTodo.css";
export default function HeaderTodoApp() {
  const navCreationContainer = useRef(null);
  const dispatch = useDispatch();
  const handleNavCreation = () => {
    navCreationContainer.current.classList.toggle("todo__header--visible");
  };
  return (
    <header className="w-full h-auto bg-gray-800 sticky top-0 z-10">
      <div className="w-full flex items-center justify-between p-3">
        <div className="w-8 h-8 relative">
          <button
            className="todo__header--btn header__overlay"
            onClick={() => handleNavCreation()}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path
                fillRule="evenodd"
                d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z"
                clipRule="evenodd"
              />
            </svg>
          </button>
          <nav
            className="w-full absolute top-0 todo__header--btns"
            ref={navCreationContainer}
          >
            <ul className="w-full list-none">
              <li className="todo__header--create">
                <button
                  className="todo__header--btn"
                  onClick={() =>
                    dispatch(openCreation(true, "todo__fig--yellow"))
                  }
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-6 w-6"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fillRule="evenodd"
                      d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z"
                      clipRule="evenodd"
                    />
                  </svg>
                </button>
              </li>
              <li className="todo__header--create">
                <button
                  className="todo__header--btn"
                  onClick={() => dispatch(openCreation(true, "todo__fig--red"))}
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-6 w-6"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fillRule="evenodd"
                      d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z"
                      clipRule="evenodd"
                    />
                  </svg>
                </button>
              </li>
              <li className="todo__header--create">
                <button
                  className="todo__header--btn"
                  onClick={() =>
                    dispatch(openCreation(true, "todo__fig--green"))
                  }
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-6 w-6"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fillRule="evenodd"
                      d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z"
                      clipRule="evenodd"
                    />
                  </svg>
                </button>
              </li>
              <li className="todo__header--create ">
                <button
                  className="todo__header--btn"
                  onClick={() =>
                    dispatch(openCreation(true, "todo__fig--purple"))
                  }
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-6 w-6"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fillRule="evenodd"
                      d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z"
                      clipRule="evenodd"
                    />
                  </svg>
                </button>
              </li>
            </ul>
          </nav>
        </div>
        <img src={logo} alt="logo" width="35" height="35" />
      </div>
    </header>
  );
}
