const submitItem = (value) => {
  return {
    type: "SUBMIT",
    payload: value,
  };
};

const openCreation = (value, todoType) => {
  return {
    type: "OPEN",
    payload: value,
    stTodo: todoType,
  };
};

const CloseCreation = (value, currentForm) => {
  return {
    type: "CLOSE",
    payload: value,
    form: currentForm,
  };
};
const CloseModification = (value, item, mForm) => {
  return {
    type: "CLOSEMODIFY",
    payload: value,
    item: item,
    form: mForm,
  };
};

const editItem = (value, index) => {
  return {
    type: "EDIT",
    payload: value,
    index: index,
  };
};

const editCurrentItem = (value) => {
  return {
    type: "MODIFY",
    payload: value,
  };
};

const deleteItem = (value) => {
  return {
    type: "DELETE",
    payload: value,
  };
};

export {
  submitItem,
  openCreation,
  editItem,
  editCurrentItem,
  CloseCreation,
  CloseModification,
  deleteItem,
};
